import React, { Component } from 'react';
import './App.css';

import AppBar from './functions/appbar.function';
import InteractiveMap from './functions/interactive-map.function';

class App extends Component {
  render() {
    return (
      <div className="App">
      <AppBar />
      <InteractiveMap />
      </div>
    );
  }
}

export default App;



